@import url(https://fonts.googleapis.com/css2?family=Actor&display=swap);
/* Google Font "Actor" */
/* This was the font that was used in the design, we could change it later */

/* Affects all elements of html */
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Inter", sans-serif;
}

/* CSS Varibles for color */
:root {
  --main-color: #23189C;
  --main-color-hover: #8C56D0;
  --accent-color: #BE51E4;
  --main-dark: #575A89;
  --main-white: #ffffff;
  --black: #2b2b2b;
  --main-grey: #E5E5E5;
  --background-purple: #F7E9FF;
  --border-grey: #CACDD3;
  --field-placeholder-grey: #E1E2E4;

}

h3,
h4 {
    margin-bottom: 1rem;
    text-align: center;
}

h3 {
    font-size: 2.8rem;
    font-weight: 500;
}
h4{
    font-size: 1.6 rem;
    font-weight: 400;
    margin-bottom: 2rem;
}

body {
    background-image: url(/static/media/Background.404940c0.png);
    color: var(--black);
}
